export default "import { AfterViewInit, Component, OnDestroy, ViewChild } from \"@angular/core\";\nimport {\n    ClientSideDataSource,\n    INovaFilteringOutputs, ISortedItem, SorterDirection, TableComponent\n} from \"@nova-ui/bits\";\nimport { Subscription } from \"rxjs\";\n\ninterface IExampleTableModel {\n    position: number;\n    name: string;\n    features: any;\n    asset: string;\n    location: string;\n    status: string;\n    outages: number;\n    checks: any;\n}\n\n@Component({\n    selector: \"nui-table-sorting-example\",\n    templateUrl: \"./table-sorting.example.component.html\",\n    providers: [ClientSideDataSource],\n})\nexport class TableSortingExampleComponent implements AfterViewInit, OnDestroy {\n    public displayedColumns = [\"position\", \"name\", \"features\", \"asset\", \"location\", \"status\", \"outages\", \"checks\"];\n    public dataSource?: IExampleTableModel[] = getData();\n    public sortedColumn: ISortedItem = {\n        sortBy: \"position\",\n        direction: SorterDirection.ascending,\n    };\n    public nameSortedDesc = false;\n    @ViewChild(\"sortableTable\") sortableTable: TableComponent<IExampleTableModel>;\n    private outputsSubscription: Subscription;\n\n    constructor(public dataSourceService: ClientSideDataSource<IExampleTableModel>) {\n        dataSourceService.setData(this.dataSource);\n    }\n\n    public sortData(sortedColumn: ISortedItem) {\n        this.sortedColumn = sortedColumn;\n        this.dataSourceService.applyFilters();\n    }\n\n    public sortByName() {\n        this.nameSortedDesc = !this.nameSortedDesc;\n        this.sortedColumn = {\n            sortBy: \"name\",\n            direction: this.nameSortedDesc ? SorterDirection.descending : SorterDirection.ascending,\n        };\n    }\n\n    ngAfterViewInit() {\n        this.dataSourceService.registerComponent(this.sortableTable.getFilterComponents());\n        this.outputsSubscription = this.dataSourceService.outputsSubject.subscribe((data: INovaFilteringOutputs) => {\n            this.dataSource = data.repeat?.itemsSource;\n        });\n\n        this.dataSourceService.applyFilters();\n    }\n\n    ngOnDestroy() {\n        this.outputsSubscription.unsubscribe();\n    }\n}\n\n/** Table data */\nfunction getData(): IExampleTableModel[] {\n    return [\n        {\n            position: 1,\n            name: \"FOCUS-SVR-02258\",\n            features: [\"remote-access-vpn-tunnel\", \"tools\", \"database\", \"orion-ape-backup\", \"patch-manager01\"],\n            asset: \"Company secrets\",\n            location: \"Kyiv\",\n            status: \"Critical\",\n            outages: 17,\n            checks: [{\n                icon: \"status_up\",\n                num: 25,\n            }],\n        },\n        {\n            position: 2,\n            name: \"Man-LT-JYJ4AD5\",\n            features: [\"remote-access-vpn-tunnel\", \"tools\", \"database\", \"orion-ape-backup\", \"patch-manager01\"],\n            asset: \"Workstation\",\n            location: \"Prague\",\n            status: \"Down\",\n            outages: 71,\n            checks: [{\n                icon: \"status_critical\",\n                num: 25,\n            }],\n        },\n        {\n            position: 3,\n            name: \"FOCUS-SVR-02258\",\n            features: [\"remote-access-vpn-tunnel\", \"tools\", \"database\", \"orion-ape-backup\", \"patch-manager01\"],\n            asset: \"Workstation\",\n            location: \"London\",\n            status: \"Inactive\",\n            outages: 56,\n            checks: [{\n                icon: \"status_down\",\n                num: 25,\n            }],\n        },\n        {\n            position: 4,\n            name: \"ABN-LT-JYJ4AD5\",\n            features: [\"remote-access-vpn-tunnel\", \"tools\", \"database\", \"orion-ape-backup\", \"patch-manager01\"],\n            asset: \"Patents\",\n            location: \"NY\",\n            status: \"Warning\",\n            outages: 48,\n            checks: [{\n                icon: \"status_up\",\n                num: 25,\n            }],\n        },\n        {\n            position: 5,\n            name: \"KLM-LFT-JAD5\",\n            features: [\"remote-access-vpn-tunnel\", \"tools\", \"database\", \"orion-ape-backup\", \"patch-manager01\"],\n            asset: \"Blueprints\",\n            location: \"Austin\",\n            status: \"Active\",\n            outages: 81,\n            checks: [{\n                icon: \"status_up\",\n                num: 25,\n            }],\n        },\n    ];\n}\n";